<template>
    <div>
        <div class="page-tabbar">
            <div class="page-wrap">
                <mt-tab-container v-model="selected">
                    <mt-tab-container-item id="home">
                        <home></home>
                    </mt-tab-container-item>
                    <mt-tab-container-item id="news">
                        <news></news>
                    </mt-tab-container-item>
                    <mt-tab-container-item id="user">
                        <user></user>
                    </mt-tab-container-item>
                </mt-tab-container>
            </div>
            <mt-tabbar v-model="selected">
                <mt-tab-item id="home">
                    <img slot="icon" src="@/assets/logo.png">
                    首页
                </mt-tab-item>
                <mt-tab-item id="news">
                    <img slot="icon" src="@/assets/logo.png">
                    动态
                </mt-tab-item>
                <mt-tab-item id="user">
                    <img slot="icon" src="@/assets/logo.png">
                    我的
                </mt-tab-item>
            </mt-tabbar>
        </div>
    </div>
</template>

<script>
import Home from './components/Home';
import News from './components/News';
import User from './components/User';
export default {
    name: 'HomePage',
    components: { Home, News, User },
    data () {
        return {
            selected: 'home'
        };
    },
    created () {
    },
    methods: {
    }
};
</script>

<style lang="less">
    .page{
        &-tabbar{
            overflow: hidden;
            height: 100vh;
        }
        &-wrap{
            overflow: scroll;
            height: 100%;
        }
    }
</style>



